<template>
	<!-- 公用底部 -->
	<view class="footBox">
		<view class="uni-tabbar">
			<view class="tabbar-item" :class="currentPage === index ? 'active' : ''" v-for="(item, index) in tabbar" :key="index" @tap="changeTab(item, index)">
				<view v-if="true" class="uni-tabbar__bd">
					<view class="uni-tabbar__icon" v-if="item.pagePath !== ''">
						<image v-if="currentPage === index" class="item-img" :src="item.selectedIconPath" />
						<image v-else class="item-img" :src="item.iconPath" />
					</view>
					<view v-else class="qrCode"><image :src="item.iconPath" /></view>
				</view>
				<view class="uni-tabbar__label" v-if="item.text !== ''">{{ item.text }}</view>
			</view>
		</view>
	</view>
	<!-- end -->
</template>

<script setup>
import { ref, reactive, onMounted, nextTick, watch } from 'vue';
import { useStore } from 'vuex';
// 获取父组件值、方法
const props = defineProps({
	pagePath: {
		type: String,
		default: ''
	}
});
// ------定义变量------
const store = useStore();//vuex获取储存数据
const currentPage = ref(store.state.footStatus);  //获取储存的当前tab状态
let tabbar = ref([
	{
		pagePath: '/pages/index/index',
		iconPath: '../../static/jikuaidi.png',
		selectedIconPath: '../../static/jikuaidiActive.png',
		text: '寄快递'
	},
	{
		pagePath: '/pages/pickup/index',
		iconPath: '../../static/chakuaidi.png',
		selectedIconPath: '../../static/chakuaidiActive.png',
		text: '查快递'
	},
	// {
	// 	pagePath: '',
	// 	iconPath: 'static/qrcode.png',
	// 	selectedIconPath: 'static/qrcode.png',
	// 	text: ''
	// },
	{
		pagePath: '/pages/my/index',
		iconPath: '../../static/wode.png',
		selectedIconPath: '../../static/wodeActive.png',
		text: '我的'
	},
	// {
	// 	pagePath: '/pages/my/index',
	// 	iconPath: 'static/user.png',
	// 	selectedIconPath: 'static/userActive.png',
	// 	text: '我的'
	// }
]);
onMounted(()=>{
	console.log(store.state,'store.state')
})
// ------方法------
// 触发tab事件
const changeTab = (item, index) => {
	console.log(item, index,'底部')
	store.commit('setFootStatus',index)
	if (item.text !== '') {
		currentPage.value = index;
		// 页面跳转
		uni.redirectTo({
			url: item.pagePath,
			success: e => {},
			fail: () => {}
		});
	}
};
</script>
<style lang="scss" scoped></style>
