<template>
	<view class="orderList">
		<view class="title">运单信息</view>
		<scroll-view class="scrollView" :class="isShowMore?'maxHeight':''" scroll-y
			lower-threshold="30" @scrolltolower="LoadMoreCustomers">
			<view class="order-item" v-for="(item,index) in (isShowMore?allOrderList.data:someOrderList.data)"
				:key="index" @click='handleToOrderInfo(event,item.id)'>
				<view class="orderNumber">
					运单号：{{item.transportOrderId}}
				</view>
				<view class="area-to-area">
					<view class="sendBox">
						<view class="sendArea">{{item.senderProvince.name}}</view>
						<view class="sendName">{{item.senderName}}</view>
					</view>
					<view class="order-status">
						<view class="status">
							{{showOrderStatus(item.status)}}
						</view>
						<view class="arrow" :class="
							  [[21000,23000,23001,23005,23008].includes(item.status)?'green-arrow':'',
						      [23009,23010].includes(item.status)?'red-arrow':'',
							  [230011,22000].includes(item.status)?'gray-arrow':'']
						"></view>
					</view>
					<view class="getBox">
						<view class="getArea">{{item.receiverProvince.name}}</view>
						<view class="getName">{{item.receiverName}}</view>
					</view>

				</view>
				<view class="order-detail">
					<!-- 已揽件运输信息 -->
					<view v-if="item.status ===23001 " class="detail-item"><text>已揽件：</text>{{ item.transportOrderPointVOS.length>0?item.transportOrderPointVOS[0].info:''}}</view>
					<!-- 运输中信息 -->
					<view v-if="[23005,23008].includes(item.status) " class="detail-item">
						<text>运输中：</text>{{item.orderInfo}}</view>
					<!-- 已签收 -->
					<view v-if="item.status ===23009 " class="detail-item"><text>已签收：</text>{{item.orderInfo}}</view>
					<!-- 已拒收 -->
					<view v-if="item.status ===23010 " class="detail-item"><text>已拒收：</text>{{item.orderInfo}}</view>
					<!-- 预计上门时间 -->
					<view v-if="item.status ===23000 " class="detail-item">
						<text>预计上门时间：</text>{{item.estimatedStartTime}}</view>
					<!-- 取消时间 -->
					<view v-if="item.status ===230011 " class="detail-item"><text>取消时间：</text>{{item.updated}}</view>
					<!-- 预计送达时间 -->
					<view v-if="[23001,23005,23008,23010].includes(item.status) " class="detail-item">
						<text>预计送达时间：</text>{{item.estimatedArrivalTime}}</view>
					<!-- 已关闭时间 -->
					<view v-if="item.status ===22000 " class="detail-item"><text>关闭时间：</text>{{item.updated}}</view>
					<!-- 签收时间 -->
					<view v-if="item.status ===23009 " class="detail-item"><text>签收时间：</text>{{item.updated}}</view>
				</view>
				<!-- 功能按钮区域 -->
				<view class="btn-box">
					<!-- 待支付 -->
					<!-- 	<view class="wait-pay" v-if="item.status===21000">
						<view class="price">运费<text>¥68</text></view>
						<view class="btn">结算</view>
					</view> -->
					<view class="btn-list" @click.stop="handleSecondQi">
						<view class="btn" v-if='item.status===23000'>修改</view>
						<view class="btn" v-if='item.status===23008'>催单</view>
						<view class="btn" v-if="[23000,23001,23005,23008,23010].includes(item.status)">分享</view>
						<view class="btn" v-if='item.status===23000'>取消寄件</view>
						<view class="btn" v-if="[22000,230011].includes(item.status)">重新下单</view>
						<view class="btn" v-if="[22000,230011,23009].includes(item.status)">删除</view>
						<view class="btn" v-if='item.status===23009'>再来一单</view>
					</view>
				</view>
				<view class="paymentStatus" v-if="item.paymentStatus">
					<!-- {{item.paymentStatus ===1?'待支付':'已支付'}} -->
					<image src='../../../static/daizhifu.png'></image>
				</view>
			</view>
			<view class="see-more" @click="handleSeeMore" v-if="!isShowMore && allOrderList.data.length >0">
				查看更多
				<view class="arrow"></view>
			</view>
			<view v-else-if="isShowMore &&  !allOrderList.data.length" class="empty-data">
				<image src="../../../static/emptyData.png"></image>
				<view class="tips">没有运单~</view>
			</view>
			<!-- 加载底部 -->
			<uni-load-more :status="status" v-if="isShowMore"/>
		</scroll-view>
	</view>
</template>

<script setup>
	import {
		ref,
		reactive,
		onMounted,
	} from 'vue';
	import {
		getOrderList
	} from '@/pages/api/order.js'
	import { handleSecondQi } from '@/utils/index.js'
	let pageInfo = reactive({
		page: 1,
		pageSize: 10
	})
	let status = ref('more') //加载状态
	let scrollTop = ref(0) //顶部位置
	let isShowMore = ref(false) //是否显示更多
	let someOrderList = reactive({
		data: []
	})
	let allOrderList = reactive({
		data: []
	})
	// let height = ref('570rpx')
	onMounted(() => {
		getOrderListFunc()
	})
	const getOrderListFunc = (flag) => {
		status.value = 'loading'
		getOrderList({
			page: pageInfo.page,
			pageSize: pageInfo.pageSize,
		}).then((res) => {
			status.value = res.data.items.length < 10 ? 'no-more' : 'more'
			if (flag === 'topPull') {
				allOrderList.data = allOrderList.data.concat(res.data.items.filter) 
			}else{
				someOrderList.data = res.data.items.slice(0, 3)
				allOrderList.data = res.data.items
			}
			
			
			console.log(res, 'res')
		})
	}
	const scroll = (e) => {
		console.log(e, 'eeee')
		scrollTop.value = e.detail.scrollTop
		uni.startPullDownRefresh("success") // 开启下拉刷新 
	}
	// onPullDownRefresh(()=>{
	// 	console.log('反反复复付')
	// })
	//查看更多
	const handleSeeMore = () => {
		console.log(111)
		isShowMore.value = true
	}
	//根据状态去显示对应的运单状态文案
	const showOrderStatus = (status) => {
		switch (status) {
			case 21000:
				return '待支付';
			case 23000:
				return '待揽件';
			case 230011:
				return '已取消';
			case 23001:
				return '已揽件';
			case 23005:
				return '运输中';
			case 22000:
				return '已关闭';
			case 23008:
				return '派送中';
			case 23009:
				return '已签收';
			case 23010:
				return '已拒收';
		}
	}
	//跳转到订单详情页面
	const handleToOrderInfo = (event,id) => {
		console.log(event,id,'fuckkkk')
		uni.navigateTo({
			url: '/subPages/order-info/index?orderId=' + id
		});
	}
	
	//
	const LoadMoreCustomers = ()=>{
		pageInfo.page = pageInfo.page + 1
		if (status.value === 'no-more') {
			return
		}
		getOrderListFunc('topPull')
	}
</script>

<style src="./orderList.scss" lang="scss" scoped></style>
